* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: white;
}
body{
  background-color: black;
}
header {
  padding: 20px 10px;
  font-weight: bold;
  font-size: 2rem;
  width: 60%;
  max-width: 600px;
  margin: auto;
  text-align: center;
}

.list {
  padding: 20px 10px;
  display: flex;
  flex-wrap: wrap;
  width: 60%;
  max-width: 350px;
  margin: auto;
  position: relative;
}

.listname {
  width: calc(100% - 100px);
  font-weight: bold;
  font-size: 1.5rem;
}
.good{
  position: absolute;
  right: 10px;
  bottom: 20px;
}
.good>span {
  cursor: pointer;
}

.date {
  width: 100%;
  font-size: 0.75rem;
  padding-top: 10px;
}

.modal {
  background-color: black;
  padding: 20px;
  position: absolute;
  top: 100px;
  right: 50%;
  transform: translate(50%,50%);
}


button {
  border: none;
  width: 100px;
  height: 50px;
}

.input {
  max-width: 350px;
  width: 60%;
  margin: auto;
  text-align: center;
  padding: 10px 0;
  display: flex;
  position: relative;
}
.i_inner{
  padding-left: 10px;
}
.i_inner div{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px;
}
input {
  height: 20px;
  width: 100px;
  background: none;
}

input:focus {
  border: transparent;
}
.i_txt input,.i_name input{
  margin: 10px;
  border: none;
  border-bottom: 1px solid white;
}

.good,
.bad {
  width: 50px;
}
.list button{
  border: none;
  width: 50px;
  height: 25px;
  background:none;
  position: absolute;
  right: 50px;
  bottom: 18px;
}

.txt {
  width: 50%;
}





a{
  right: 0;
  position: relative;
  display: inline-block;
  padding: 10px 10px;
  margin: 40px 0;
  color: #03e9f4;
  text-decoration: none;
  text-transform: uppercase;
  transition: 0.5s;
  letter-spacing: 4px;
  overflow: hidden;
  margin-right: 50px;
  filter: hue-rotate(270deg);
 
}
a:hover{
  background: #03e9f4;
  color: #050801;
  box-shadow: 0 0 5px #03e9f4,
              0 0 25px #03e9f4,
              0 0 50px #03e9f4,
              0 0 200px #03e9f4;
   -webkit-box-reflect:below 1px linear-gradient(transparent, #0005);
}
a span{
  position: absolute;
  display: block;
}
a span:nth-child(1){
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg,transparent,#03e9f4);
  animation: animate1 1s linear infinite;
}
@keyframes animate1{
  0%{
      left: -100%;
  }
  50%,100%{
      left: 100%;
  }
}
a span:nth-child(2){
  top: -100%;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg,transparent,#03e9f4);
  animation: animate2 1s linear infinite;
  animation-delay: 0.25s;
}
@keyframes animate2{
  0%{
      top: -100%;
  }
  50%,100%{
      top: 100%;
  }
}
a span:nth-child(3){
  bottom: 0;
  right: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg,transparent,#03e9f4);
  animation: animate3 1s linear infinite;
  animation-delay: 0.50s;
}
@keyframes animate3{
  0%{
      right: -100%;
  }
  50%,100%{
      right: 100%;
  }
}


a span:nth-child(4){
  bottom: -100%;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(360deg,transparent,#03e9f4);
  animation: animate4 1s linear infinite;
  animation-delay: 0.75s;
}
@keyframes animate4{
  0%{
      bottom: -100%;
  }
  50%,100%{
      bottom: 100%;
  }
}